.main {
      display: flex;
      flex-direction: column;
      user-select: none;
      align-items: center;
      justify-content: center;
}

h1 {
      font-size: 2rem;
      text-align: center;
      font-family: cursive;
      color: #06201b;
      margin-bottom: 10px;
}
hr {
      width: 50%;
      margin: auto;
      border: 2px solid;
      padding: 5px;
      background-color: #3ad29f;
      border: none;
      margin-bottom: 10px;
}

h3 {
      font-size: 2rem;
      text-align: center;
      font-family: cursive;
      color: #06201b;
      width: 100%;
      margin-bottom: 10px;
}
P {
      font-size: 1rem;
      text-align: center;
      font-family: cursive;
      color: #06201b;
      width: 100%;
      margin-bottom: 10px;
}
.span {
      cursor: pointer;
      transition: 0.5sec;
      color: black;
      font-weight: bold;
}
.span:hover {
      transform: scale(2);
}
input {
      padding: 1rem;
      margin: 20px 0;
      width: 80%;
      outline: none;
      border: none;
      border: 2px solid #06201b;
      font-family: cursive;
      border-radius: 1rem;
      text-decoration: none;
      background: transparent;
      transition: 1sec;
}
input:hover {
      transform: scale(1.05);
}

button {
      padding: 1rem 3rem;
      border-radius: 50px;
      color: black;
      background-color: #3ad29f;
      border: none;
      font-size: 1rem;
      cursor: pointer;
      transition: 0.5sec;
      font-family: cursive;
}

button:hover {
      transform: scale(1.1);
}
